import * as React from 'react';
import { IAny, ICoreContext, IGeneric } from '../index';

export type ComponentType =
    | 'Price'
    | 'Rating'
    | 'AddToCart'
    | 'CartIcon'
    | 'WishListIcon'
    | 'WishList'
    | 'Product'
    | 'CartLineItem'
    | 'StockAvailabity';

export interface IComponent<T extends IComponentProps> {
    component: React.FC<T>;
}

// tslint:disable-next-line:no-any
export interface IComponentProps<T = any> {
    context: ICoreContext<IGeneric<IAny>>;
    // typename and id contains module typename component called from
    id: string;
    typeName: string;
    data: T;
}
